#work {
	display: flex;
	flex-direction: column;
	padding-top: 5vh;
	background: rgb(240, 246, 255);
	background: linear-gradient(
		0deg,
		rgba(240, 246, 255, 1) 0%,
		rgba(255, 255, 255, 1) 100%
	);
}
#work h1 {
	text-align: center;
	color: #242227;
	margin-top: 10vh;
}
.card-box {
	display: flex;
	flex-direction: column;
	width: 70%;
	min-width: 900px;
	margin: 50px auto;
}
.card {
	width: 100%;
	min-height: 400px;
	border-radius: 15px;
	display: flex;
	flex-direction: row;
	background-color: white;
	margin-bottom: 80px;
	-webkit-box-shadow: 0px 2px 18px #d7dbd8;
	box-shadow: 0px 2px 18px #d7dbd8;
}

.card .project-image {
	width: 50%;
	height: 100%;
}
.card .project-image img {
	display: block;
	width: 100%;
	height: 100%;
	object-fit: cover;
	border-top-left-radius: 15px;
	border-bottom-left-radius: 15px;
}
.card:nth-child(odd) .project-image {
	order: 2;
}
.card:nth-child(odd) .project-image img {
	border-radius: 0;
	border-top-right-radius: 15px;
	border-bottom-right-radius: 15px;
}
.card .project-info {
	width: 50%;
	display: flex;
	flex-direction: column;
	border-radius: 15px;
}

.card .project-info h2,
.card .project-info p {
	padding: 10px;
	margin: 15px;
	color: #242227;
}
.card .project-info h2 {
	font-size: 34px;
	text-align: center;
	height: fit-content;
}
.card .project-info ul,
.card .project-info ul li {
	margin: 0;
	padding: 0;
}
.card .project-info ul {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
}
.card .project-info ul li {
	margin: 5px;
	list-style-type: none;
	color: #8e8a94;
}
.card .project-info ul li:not(:last-child)::after {
	content: ",";
}
.card .project-info .project-links {
	margin: auto auto 10px;
	display: flex;
	flex-direction: column;
}
.card .project-info .project-links a {
	color: #8e8a94;
	width: 120px;
	padding: 5px;
	text-align: center;
	line-height: 24px;
	margin: 0 auto 10px auto;
}
.card .project-info .project-links a:nth-child(1) {
	text-decoration: none;
	background: rgb(135, 171, 204);
	background: linear-gradient(
		90deg,
		rgba(135, 171, 204, 1) 2%,
		rgba(85, 193, 155, 1) 64%
	);
	color: white;
	border-radius: 25px;
	width: 240px;
	height: 35px;
	font-size: 22px;
	transition: ease 0.2s;
}
.card .project-info .project-links .project-note {
	color: #8e8a94;
	font-size: 12px;
}
.second {
	background: rgb(74, 68, 138) !important;
	background: linear-gradient(
		90deg,
		rgba(74, 68, 138, 1) 2%,
		rgba(188, 101, 164, 1) 64%
	) !important;
}
.card .project-info .project-links a:hover {
	opacity: 0.85;
}
@media only screen and (max-width: 1000px) {
	.card-box {
		width: 70%;
		min-width: 300px;
	}
	.card {
		flex-direction: column;
	}
	.card:nth-child(n) .project-image {
		order: initial;
	}
	.card .project-image,
	.card .project-info {
		width: 100%;
	}
	.card .project-image img {
		height: 390px;
	}
	.card .project-image img:nth-child(n) {
		border-radius: 0;
		border-top-left-radius: 15px;
		border-top-right-radius: 15px;
	}
}
